@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;200&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5vh;
  color: rgb(45, 62, 163);
}

.coin-search {
  margin-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 3vh;
  margin-top: 3vh;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  font-size: 24px;
  border-radius: 6px;
  border: none;
  outline: none; /* remove frame from search box */
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #bce618 100%
  );

  color: black;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.header {
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: space-around;
  width:80vw;
  height:8vh;
  background:rgb(217, 221, 163);
  position: relative;
}
.header > P {
  background:rgb(217, 221, 163);
  color:#020225;
  font-size: 20px;
  margin-left: 60px;
  font-weight: 500;
  position:absolute;
}

.last-update {
  right: 190px;

}

.volume {
 right:470px ;
}
.change {
right:10px;
}

.name {
  left:-20px;
}

.symbol {
 left:140px;
}
.price {
  left:280px;
}


.coin-container {
    display: flex;
    justify-content: center;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px dashed #d7d7d7;
    width: 1100px;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
  }
  
  .coin h1 {
    font-size: 16px;
    width: 150px;
    color :rgb(189, 189, 77);
    padding-left: 10px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
    padding-left: 20px;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: start;
    width: 100%;
    margin-right:40px;
    margin-left:40px;

  }
  
  .coin-price {
    width: 70px;
    
  }
  
  .coin-volume {
    width: 100%;
    margin-left: 100px;
    text-align:center;

  }
  .coin-last-update {
      width: 100%;
      margin-left: none;
      margin-right: 100px;

  }
  
  .coin-percent {
    width: 100px;
    margin-right:10px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
